"use client";
import Link from "next/link";
import { clearAuth, getAuth } from "../lib/auth";
import { useEffect, useState } from "react";

export default function Nav() {
  const [auth, setAuth] = useState<{ uid: string; token: string } | null>(null);

  // Read auth only on the client after mount to avoid SSR / hydration mismatch
  useEffect(() => {
    setAuth(getAuth());
  }, []);

  return (
    <div style={{ borderBottom: "1px solid rgba(255,255,255,.1)" }}>
      <div className="container row" style={{ justifyContent: "space-between" }}>
        <b>Main St AI</b>
        <div className="row">
          <Link href="/dashboard">Dashboard</Link>
          <Link href="/contacts">Contacts</Link>
          <Link href="/chat">Chat</Link>
          <Link href="/owner-cover">Owner Cover</Link>
          {auth ? (
            <button
              className="btn"
              onClick={() => {
                clearAuth();
                location.href = "/login";
              }}
            >
              Logout
            </button>
          ) : (
            <Link href="/login">Login</Link>
          )}
        </div>
      </div>
    </div>
  );
}
